'use client';

import React, { useEffect, useState } from 'react';

const LOADING_PHASES = [
  "Initializing core...",
  "Loading assets...",
  "Verifying integrity...",
  "Welcome."
];

export default function Template({ children }: { children: React.ReactNode }) {
  const [progress, setProgress] = useState(0);
  const [isInternalExit, setIsInternalExit] = useState(false); // Controls text fade-out before slide
  const [isExiting, setIsExiting] = useState(false); // Controls the slide-up animation
  const [isComplete, setIsComplete] = useState(false); // Removes loader from DOM
  const [phaseIndex, setPhaseIndex] = useState(0);

  useEffect(() => {
    // 1. Counter Animation
    const timer = setInterval(() => {
      setProgress((prev) => {
        // Random increment for organic feel
        const next = prev + Math.floor(Math.random() * 12) + 4;
        
        if (next >= 100) {
          clearInterval(timer);
          return 100;
        }
        return next;
      });
    }, 60);

    // 2. Phase Text Cycler
    const phaseTimer = setInterval(() => {
        setPhaseIndex(p => (p + 1) % LOADING_PHASES.length);
    }, 250);

    return () => {
        clearInterval(timer);
        clearInterval(phaseTimer);
    };
  }, []);

  // Watch for progress completion
  useEffect(() => {
    if (progress === 100) {
        // Sequence: 100% -> Fade Content -> Slide Curtain -> Remove from DOM
        setTimeout(() => {
            // 1. Fade out the text/counter first
            setIsInternalExit(true);

            // 2. Wait for fade to finish, then slide the curtain up
            setTimeout(() => {
                setIsExiting(true);
                
                // 3. Finally remove component from DOM
                setTimeout(() => setIsComplete(true), 1000);
            }, 500);
        }, 200);
    }
  }, [progress]);

  return (
    <>
      {!isComplete && (
          <div 
            className={`
                fixed inset-0 z-[999] bg-[#050505] flex flex-col items-center justify-center
                transition-transform duration-[800ms] ease-[cubic-bezier(0.83,0,0.17,1)]
                ${isExiting ? '-translate-y-full' : 'translate-y-0'}
            `}
          >
             {/* Content Container - Staggered fade out with slight upward drift */}
             <div 
                className={`
                    flex flex-col items-center gap-8 transition-all duration-700 ease-out
                    ${isInternalExit ? 'opacity-0 -translate-y-10 scale-95' : 'opacity-100 translate-y-0 scale-100'}
                `}
             >
                 
                 {/* 1. Large Counter */}
                 <div className="relative">
                    <span className="text-8xl md:text-9xl font-bold font-display tracking-tighter text-white tabular-nums">
                        {progress}
                    </span>
                 </div>

                 {/* 2. Progress Bar */}
                 <div className="w-64 h-[2px] bg-white/10 rounded-full overflow-hidden relative">
                     <div 
                        className="absolute inset-y-0 left-0 bg-[#ccff00] transition-all duration-100 ease-out"
                        style={{ width: `${progress}%` }}
                     />
                     {/* Moving glow effect on the bar */}
                     <div 
                        className="absolute inset-y-0 w-20 bg-white/50 blur-[6px] transition-all duration-100 ease-out"
                        style={{ left: `${progress}%`, transform: 'translateX(-50%)' }}
                     />
                 </div>

                 {/* 3. Status Text */}
                 <div className="h-6 overflow-hidden flex flex-col items-center">
                    <div className="text-xs font-mono text-gray-500 uppercase tracking-[0.2em] animate-pulse">
                        {progress === 100 ? "Ready" : LOADING_PHASES[Math.min(phaseIndex, LOADING_PHASES.length - 1)]}
                    </div>
                 </div>
             </div>
             
             {/* Background decorative grid/noise */}
             <div className="absolute inset-0 bg-[url('/noise.png')] opacity-[0.03] pointer-events-none"></div>
             
             {/* Optional: Subtle vertical lines for "tech" feel */}
             <div className="absolute inset-y-0 left-10 w-[1px] bg-white/5"></div>
             <div className="absolute inset-y-0 right-10 w-[1px] bg-white/5"></div>
          </div>
      )}

      {/* Main Content - Scales up slightly when loader lifts */}
      <div 
        className={`
            transition-all duration-[1000ms] ease-[cubic-bezier(0.25,1,0.5,1)] 
            ${!isComplete ? 'scale-95 opacity-0 brightness-50 grayscale' : 'scale-100 opacity-100 brightness-100 grayscale-0'}
        `}
      >
         {children}
      </div>
    </>
  );
}